import * as R from 'ramda'

export const getWetsuitById = (state, id) => R.prop(id, state.wetsuits)

export const getWetsuits = (state, ownProps) => {
    const activeCategoryID = getActiveCategoryID(ownProps)
    const activePriceRangyID = getActivePriceRangyID(ownProps)
    const applySearch = item => R.contains(
        state.wetsuitsPage.search.toLowerCase(),
        R.prop('name', item).toLowerCase()
    )
    const applyCategory = item => R.equals(
        activeCategoryID,
        R.prop('categoryId', item)
    )
    const applyPriceRangy = item => R.equals(
        activePriceRangyID,
        R.prop('priceId', item)
    )
    const wetsuits = R.compose(
        R.filter(applySearch),
        R.when(R.always(activeCategoryID), R.filter(applyCategory)),
        R.when(R.always(activePriceRangyID), R.filter(applyPriceRangy)),
        R.map(id => getWetsuitById(state, id))
    )(state.wetsuitsPage.ids)
    return wetsuits
}

export const getRenderedWetsuitsLenght = state => R.length(state.wetsuitsPage.ids)

export const getCategories = state => R.values(state.categories)

export const getPriceRange = state => R.values(state.priceRange)

export const getActiveCategoryID = ownProps => R.path(['match', 'params', 'id'], ownProps)

export const getActivePriceRangyID = ownProps => R.path(['match', 'params', 'priceRange'], ownProps)