import React from 'react'
import {Switch, Route} from 'react-router'

import wetsuits from 'containers/wetsuits'
import wetsuit from 'containers/wetsuit'

export default (
    <Switch>
        <Route path='/' component={wetsuits} exact />
        <Route path='/categories/:id' component={wetsuits} />
        <Route path='/priceRange/:priceRange' component={wetsuits} />
        <Route path='/wetsuits/:id' component={wetsuit} />
    </Switch>
)