import * as R from 'ramda'

import {
    FETCH_WETSUITS_SUCCESS, 
    LOAD_MORE_WETSUITS_SUCCESS,
    SEARCH_WETSUIT
} from 'actionTypes'

const initialState = {
    ids: [],
    search: ''
}

export default (state = initialState, {type, payload}) => {
    switch (type) {
        case FETCH_WETSUITS_SUCCESS:
            return R.merge(state, {
                ids: R.pluck('id', payload)
            })
        case LOAD_MORE_WETSUITS_SUCCESS:
            const ids = R.pluck('id', payload)
            return R.merge(state, {
                ids: R.concat(ids, state.ids)
            })
        case SEARCH_WETSUIT:
            return R.merge(state, {
                search: payload
            })
        default:
            return state;
    }
}