import React, {Component} from 'react'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import * as R from 'ramda'

import {
    fetchWetsuits, 
    loadMoreWetsuits,
    fetchCategories,
    fetchPriceRange
} from 'actions'
import {getWetsuits} from 'selectors'
import Layout from 'containers/layout'

class wetsuits extends Component {
    
    constructor(props){
        super(props)
        this.state = {
            toggleDisplayActive: false 
        }
    }

    componentDidMount(){
        this.props.fetchWetsuits()
        this.props.fetchCategories()
        this.props.fetchPriceRange()
    }

    handleToggleDisplay() {
        this.setState(prevState => ({
            toggleDisplayActive: !prevState.toggleDisplayActive
        })) 
        console.log(this.state.toggleDisplayActive)
    }

    renderWetsuits(wetsuits, index) {
        const shortDescription = `${R.take(60, wetsuits.description)}...`

        return (
            <div className={this.state.toggleDisplayActive === false ? 'col-sm-4 col-lg-4 col-md-4 book-list' : 'col-sm-12 col-lg-12 col-md-12 book-list'} key={index}>
                <div className='thumnail'>
                    <img
                        className= {this.state.toggleDisplayActive === false ? 'img-thumbnail' : 'img-thumbnail-switch'}
                        src={wetsuits.image}
                        alt={wetsuits.name}
                    />
                    <div className={this.state.toggleDisplayActive === false ? 'caption' : 'text-center'}>
                        <h4 className='pull-right'>${wetsuits.price}</h4>
                        <h6>
                            <Link to={`/wetsuits/${wetsuits.id}`}>
                                {wetsuits.name}
                            </Link>
                        </h6>
                        <p>{shortDescription}</p>
                        <p className='itemButton'>
                            <button className='btn btn-primary'>
                                Buy now !
                            </button>
                            <Link to={`/wetsuits/${wetsuits.id}`} className='btn btn-default'>
                                More info
                            </Link>
                        </p>
                    </div>
                </div>
            </div>
        )
    }
    render(){
        const {wetsuits, loadMoreWetsuits} = this.props
        return(
            <Layout>
                <button className='btn_switch btn btn-default'  onClick={this.handleToggleDisplay.bind(this)}>Single Vue</button>
                <div className= 'books row'>
                    {wetsuits.map((wetsuits, index) => this.renderWetsuits(wetsuits, index))}
                </div>
                <div className='row'>
                    <div className='col-md-12'>
                        <button onClick={loadMoreWetsuits} className='pull-right btn btn-primary'>
                            Load More
                        </button>
                    </div>
                </div>
            </Layout>
        )
    }
} 

const mapStateToProps = (state, ownProps) => ({
    wetsuits: getWetsuits(state, ownProps)
})

const mapDispatchToProps = {
    fetchWetsuits,
    loadMoreWetsuits,
    fetchCategories,
    fetchPriceRange
}

export default connect(mapStateToProps, mapDispatchToProps)(wetsuits)