import React from 'react'
import {connect} from 'react-redux'
import {withRouter} from 'react-router'
import {compose} from 'redux'
import {Link} from 'react-router-dom'
import classNames from 'classnames'
import * as R from 'ramda' 

import {getPriceRange, getActivePriceRangyID} from 'selectors'

const PriceRange = ({priceRange, activePriceRangyId}) => {
    const renderPriceRangy = (priceRangy, index) => {
        const getActiveState = R.propEq('id', activePriceRangyId)
        const linkClass = classNames({
            'list-group-item': true,
            'active' : getActiveState(priceRangy)
        })
        return (
            <Link 
            to={`/priceRange/${priceRangy.id}`} 
            className={linkClass} 
            key={index}
            >
                {priceRangy.name}
            </Link>
        )
    }

    const renderAllPriceRangy = () => {
        const linkClass = classNames({
            'list-group-item': true,
            'active' : R.isNil(activePriceRangyId)
        })
        return (
            <Link 
            to={'/'} 
            className={linkClass} 
            >
                Any
            </Link>
        )
    }

    return (
        <div className='well'>
            <h4>Price Range</h4>
            <div className='list-group'>
                {renderAllPriceRangy()}
                {priceRange.map((priceRangy, index) => renderPriceRangy(priceRangy, index))}
            </div>
        </div>
    )
}

const mapStateToProps = (state, ownProps) => ({
    priceRange: getPriceRange(state),
    activePriceRangyId: getActivePriceRangyID(ownProps)
})

export default compose(
    withRouter,
    connect(mapStateToProps, null)
)(PriceRange)

