import * as R from 'ramda'
import request from 'superagent'

import wetsuits from 'api/mockWetsuits'
import categories from 'api/mockCategories'
import priceRange from 'api/mockPriceRange'

export const fetchWetsuits = async () => {
    const {body} = await request.get('http://www.mocky.io/v2/5de684da3700005400092451')
    return body.wetsuits
}

export const loadMoreWetsuits = async ({offset}) => {
    const {body} = await request.get('http://www.mocky.io/v2/5de684da3700005400092451')
    return body.wetsuits
}

export const fetchWetsuitById = async (id) => {
    const {body} = await request.get('http://www.mocky.io/v2/5de684da3700005400092451')
    const wetsuit = R.find(R.propEq('id', id), body.wetsuits)
    return wetsuit
    
}

export const fetchCategories = async () => {
    return new Promise(resolve => {
        resolve(categories)
    })
}

export const fetchPriceRange = async () => {
    return new Promise(resolve => {
        resolve(priceRange)
    })
}