import{
    FETCH_WETSUITS_START,
    FETCH_WETSUITS_SUCCESS, 
    FETCH_WETSUITS_FAILURE,
    LOAD_MORE_WETSUITS_START,
    LOAD_MORE_WETSUITS_SUCCESS,
    LOAD_MORE_WETSUITS_FAILURE,
    FETCH_WETSUITS_BY_ID_START,
    FETCH_WETSUITS_BY_ID_SUCCESS,
    FETCH_WETSUITS_BY_ID_FAILURE,
    SEARCH_WETSUIT,
    FETCH_CATEGORIES_START,
    FETCH_CATEGORIES_SUCCESS,
    FETCH_CATEGORIES_FAILURE,
    FETCH_PRICE_RANGE_START,
    FETCH_PRICE_RANGE_SUCCESS,
    FETCH_PRICE_RANGE_FAILURE
} from 'actionTypes'
import{
    fetchWetsuits as fetchWetsuitsApi,
    loadMoreWetsuits as loadMoreWetsuitsApi,
    fetchWetsuitById as fetchWetsuitByIdApi,
    fetchCategories as fetchCategoriesApi,
    fetchPriceRange as fetchPriceRangeApi
} from 'api'
import {getRenderedWetsuitsLenght} from 'selectors'

export const fetchWetsuits = () => async dispatch => {
    dispatch({type: FETCH_WETSUITS_START})
    try{
        const wetsuits = await fetchWetsuitsApi()

        dispatch({
            type: FETCH_WETSUITS_SUCCESS,
            payload: wetsuits
        })
    }
    catch(err){
        dispatch({
            type: FETCH_WETSUITS_FAILURE,
            payload: err,
            error: true
        })
    }
}

export const loadMoreWetsuits = () => async (dispatch, getState) => {
    const offset = getRenderedWetsuitsLenght(getState())
    dispatch({type: LOAD_MORE_WETSUITS_START})
    try{
        const wetsuits = await loadMoreWetsuitsApi({offset})
        dispatch({
            type: LOAD_MORE_WETSUITS_SUCCESS,
            payload: wetsuits
        })
    }
    catch(err){
        dispatch({
            type: LOAD_MORE_WETSUITS_FAILURE,
            payload: err,
            error: true
        })
    }
}

export const fetchWetsuitById = (id) => async dispatch => {
    dispatch({type: FETCH_WETSUITS_BY_ID_START})
    try{
        const wetsuit = await fetchWetsuitByIdApi(id)
        dispatch({
            type: FETCH_WETSUITS_BY_ID_SUCCESS,
            payload: wetsuit
        })
    }
    catch(err){
        dispatch({
            type: FETCH_WETSUITS_BY_ID_FAILURE,
            payload: err,
            error: true
        })
    }
}

export const searchWetsuit = text => dispatch => {
    dispatch({
        type: SEARCH_WETSUIT,
        payload: text
    })
}

