import React, {Component} from 'react'
import {connect} from 'react-redux'
import * as R from 'ramda'
import {Link} from 'react-router-dom'

import {fetchWetsuitById} from 'actions'
import {getWetsuitById} from 'selectors'

class wetsuit extends Component {
    componentDidMount() {
        const wetsuitId = this.props.match.params.id
        this.props.fetchWetsuitById(wetsuitId)
    }

    renderField() {
        const {wetsuit} = this.props
        const columnField = R.compose(
            R.toPairs,
            R.pick([
                'gender',
                'size',
                'thickness',
                'type',
                'zip_type'
            ])
        )(wetsuit)

        console.log('columns', columnField)

        return columnField.map(([key, value]) => (
            <div className='column' key={key}>
                <div className='ab-details-title'>
                    <p>{key}</p>
                </div>
                <div className='ab-details-info'>
                    {value}
                </div>
            </div>
        ))
    }

    renderContent(){
        const {wetsuit} = this.props

        return (
            <div className='thumbnail'>
                <div className='row'>
                    <div className='col-md-6'>
                        <img
                            className='img-thumbnail'
                            src={wetsuit.image}
                            alt={wetsuit.name}
                        />
                    </div>
                    <div className='col-md-6'>
                        {this.renderField()}
                    </div>
                </div>
                <div className='caption-full'>
                    <h4 className='pull-right'>${wetsuit.price}</h4>
                    <h4>{wetsuit.name}</h4>
                    <p>{wetsuit.description}</p>
                </div>
            </div>
        )
    }

    renderSidebar(){
        const {wetsuit} = this.props
        
        return (
            <div>
                <p className='lead'>Quick shop</p>
                <div className='form-group'>
                    <h1>{wetsuit.name}</h1>
                    <h2>${wetsuit.price}</h2>
                </div>
                <Link to='/' className='btn btn-info btn-block'>
                    Back to store
                </Link>
            </div>
        )
    }

    deleteWetsuit(event) {
        this.props.actions.deleteWetsuit(this.state.wetsuit)
    }

    render() {
        return (
            <div className='view-container'>
                <div className='container'>
                    <div className='row'>
                        <div className='col-md-9'>
                            {this.props.wetsuit && this.renderContent()}
                        </div>
                        <div className='col-md-3'>
                            {this.props.wetsuit && this.renderSidebar()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        wetsuit:getWetsuitById(state, state.wetsuitPage.id)
    }
}

const mapDispatchToProps = {
    fetchWetsuitById
}

export default connect (mapStateToProps, mapDispatchToProps) (wetsuit)